<template>
	<div class="average-content">
		<div class="tips">
			描述:
			<p>1.(总重量-容器重量*容器数量)/单个重量(G)=大约数量</p>
			<p>2.锁定，锁定为相同的值方便不需要多次输入</p>
			<p>3.每次结算生成一个小列表描述当前结算的信息</p>
			<p>4.总数量是结算所有结算结果的集合</p>
		</div>
			<form action="" class="form-one">
				<!--产品名字-->
				<average-item :num-info="numInfo"  :text-info="productInfo" @keepClick="productClick"/>
				<!--总重量-->
				<average-item :num-info="numInfo"  :text-info="totalWeightInfo" @keepClick="totalKeepClick"/>
				<!--容器重量-->
				<average-item :num-info="numInfo" :text-info="vesselWeightInfo"  @keepClick="vesselKeepClick"/>
				<!--容器数量-->
				<average-item :num-info="numInfo" :text-info="vesselNumInfo" @keepClick="numKeepClick"/>
				<!--单个重量-->
				<average-item :num-info="numInfo" :text-info="oneWeightInfo"  @keepClick="oneKeepClick"/>
				<div class="bnt-box">
					<button class="seed-icon" type='button' @click="resultClick">结算</button>
					<span class="result-box">总数量：<i class="result-num">{{keepArrsAll}}</i> 个</span>
				</div>
			</form>
		<div class="show-div" v-show="keepArr.length">
			<h3 class="show-div-title">结算列表({{keepArrs.length}})</h3>
			<div class="product-tag-box">
				<i v-for="(item,index) in productArr" :key="index" @click="iconClick(item.name,index)" :class="{currentclass:currentIndex==index}">
					{{item.name}}({{item.num}})
				</i>
			</div>
			<div v-for="(item,index) in keepArrs" :key="index" class="item-div">
				<h5 class="item-title">第{{item.keepNum}}次结算</h5>
				<ul class="item-ul">
					<li>产品名字：{{item.productName}}</li>
					<li>总重量：{{item.totalWeight}}KG</li>
					<li>容器重量：{{item.vesselWeight}}KG</li>
					<li>容器数量：{{item.vesselNum}}个</li>
					<li>单个重量：{{item.oneWeight}}G</li>
					<li>结果个数：{{item.result}}个</li>
				</ul>
			</div>
		</div>
	</div>
</template>

<script>
import AverageItem from './AverageItem'

export default {
  name: 'AverageContent',
  components:{
	AverageItem
  },
  data(){
	return{
		numInfo:{
			productName:'',
			totalWeight:'',						//总重量
			oneWeight:'',						//单个重量
			vesselWeight:'',					//容器重量
			vesselNum:'',						//容器数量
			result:0,
			keepNum:0
		},
		//产品名字
		productInfo:{
			title:'产品名字',
			placeholderText:'请输入产品名字',
			name:'productName',
			unitText:'字',
			isKeep:false
		},
		//总重量
		totalWeightInfo:{
			title:'总重量',
			placeholderText:'请输入总重量',
			name:'totalWeight',
			unitText:'KG',
			isKeep:false
		},
		//容器重量
		vesselWeightInfo:{
			title:'容器重量',
			placeholderText:'请输入容器重量量',
			name:'vesselWeight',
			unitText:'KG',
			isKeep:false
		},
		//容器数量
		vesselNumInfo:{
			title:'容器数量',
			placeholderText:'请输入容器数量',
			unitText:'个',
			name:'vesselNum',
			isKeep:false
		},
		//单个重量
		oneWeightInfo:{
			title:'单个重量',
			placeholderText:'请输入单个重量',
			name:'oneWeight',
			unitText:'G',
			isKeep:false
		},
		//结果
		keepArr:[],				//这个是保存点击的每一个结果
		productArr:[
		],
		currentIndex:-1,				//当前选中
		tag:'',
	}
  },
  methods:{
	//结果点击
	resultClick(){
		let info=this.numInfo
		//0.
		if(!this.checkIsNum(info.productName,'产品名称',true)){
			return false
		}
		//1.
		if(!this.checkIsNum(info.totalWeight,'总重量')){
			return false
		}
		//3.
		if(!this.checkIsNum(info.vesselNum,'容器数量')){
			return false
		}
		//4.
		if(!this.checkIsNum(info.vesselWeight,'容器重量')){
			return false
		}
		//2.
		if(!this.checkIsNum(info.oneWeight,'单个重量')){
			return false
		}
		
		//输入都通过往下执行
		info.keepNum++										//这个是储存计算第几次的数量
		let g=1000;											//克是1000/1千克
		info.result=parseInt((info.totalWeight-info.vesselWeight*info.vesselNum)/(info.oneWeight/g))			//计算(取整)
		//拷贝对象-保存到对象里面
		let obj = JSON.parse(JSON.stringify(info));				//这里浅拷贝为了不影响 原来对象
		//这里保存一下结果-保存了多少次
		this.keepArr.push(obj)
		
		
		//插入名字
		//名字是否已经存在
		let index=this.productArr.findIndex(item=>item.name==info.productName)
		if(index!=-1){
			this.productArr[index].num++
		}else{
			this.productArr.push({
				name:info.productName,
				num:1
			})
		}
		
		//保存完成之后-清空四个选项
		this.isKeepSet('totalWeightInfo','totalWeight')				
		this.isKeepSet('productInfo','productName')				
		this.isKeepSet('vesselNumInfo','vesselNum')
		this.isKeepSet('oneWeightInfo','oneWeight')
		this.isKeepSet('vesselWeightInfo','vesselWeight')
		
		
		//console.log(this.keepArr)
	},
	//锁定和清空
	isKeepSet(type,value){
		if(this[type].isKeep==false){
			this.numInfo[value]=''
		}
	},
	//验证是否数字
	checkIsNum(num,info,type=false){
		if(num == ""|| num ==null){
			this.$toast.show(info+'不能为空',1500)
			return false;
		}
		if(type===true){
			return true
		}
		if(isNaN(num)){
			this.$toast.show(info+'必须是数字',1500)
			return false
		}
		return true
	},
	//
	productClick(){
		this.productInfo.isKeep=!this.productInfo.isKeep
	},
	vesselKeepClick(){
		this.vesselWeightInfo.isKeep=!this.vesselWeightInfo.isKeep
	},
	oneKeepClick(){
		this.oneWeightInfo.isKeep=!this.oneWeightInfo.isKeep
	},
	numKeepClick(){
		this.vesselNumInfo.isKeep=!this.vesselNumInfo.isKeep
	},
	totalKeepClick(){
		this.totalWeightInfo.isKeep=!this.totalWeightInfo.isKeep
	},
	//标签点击
	iconClick(name,index){
		this.tag=name
		if(this.currentIndex==index){
			this.currentIndex=-1
			this.tag=''
		}else{
			this.currentIndex=index
		}
	}
	//检查是否已经存在
  },
  computed:{
	keepArrs(){
		//点击-将标签名字保存到一个变量 然后通过该名字筛选数组内与对象名字相同的对象
		let finalArr=[];
		if(this.tag!=''){
			//这里可以优化一下的
			for(let item in this.keepArr){
				if(this.keepArr[item].productName==this.tag){
					finalArr.push(this.keepArr[item])
				}
			}
				return finalArr
		}else{
			//筛选数据-computed里返回该数组
			return this.keepArr
		}
	},
	keepArrsAll(){
		let res=0
		this.keepArrs.forEach(obj=>{
			res+=obj.result
		})
		return res
	}
  }
}
</script>


